import router from '@/router'
import store from '@/store'
import { Notify } from 'vant'
export function handlerHttpError (res) {
  switch (res.code) {
    case 50001: // token
      if (res.message) {
        Notify(res.message)
      }
      break
    case 50002: // token
    case 50009: // 
      if (res.message) {
        Notify(res.message)
      }
      store.commit('setLoginState', {
        whether: false,
        utype: 0,
        token: '',
        mobile: '',
        userIminfo: {}
      })
      router.push('/member/login')
      break
    case 50003: // 
      if (res.message) {
        Notify(res.message)
      }
      router.push('/member/company/profile')
      break
    case 50004: // 
      if (res.message) {
        Notify(res.message)
      }
      router.push('/member/company/auth')
      break
    case 50005: // 
      if (res.message) {
        Notify(res.message)
      }
      router.push('/member/personal/resume')
      break
    case 50006: // 
      router.push({ path: '/member/bind', query: res.data })
      break
    case 50007: // 
      if (res.message) {
        Notify(res.message)
      }
      let url =
        store.state.config.personal_reg_mode == 1
          ? '/member/personal/resume_add_form/step1'
          : '/member/personal/resume_add_int/step1'
      router.push(url)
      break
    case 50008: // 
      router.push('/error?message=' + res.data)
      break
    default:
      if (res.message) {
        Notify(res.message)
      }
      break
  }
}



