<template>
  <div>

  </div>
</template>

<script>
export default {
  name: 'CaptchaVaptcha',
  data () {
    return {

    }
  },
  mounted () {
    if (window.vaptcha === undefined) {
      let script = document.createElement('script')
      let head = document.getElementsByTagName('head')[0]
      script.type = 'text/javascript'
      script.charset = 'UTF-8'
      script.src = 'https://v.vaptcha.com/v3.js'
      head.appendChild(script)
    }
  },
  created () {},
  computed: {},
  methods: {
    show (callback) {
      let that = this
      if (that.$store.state.config.captcha_vaptcha_vid == '') {
        this.$notify('vid')
        return false
      }
      window.vaptcha({
        vid: that.$store.state.config.captcha_vaptcha_vid, // id
        type: 'invisible', //  
        scene: 0, //  0
        offline_server: '' // 
        // 
        // lang: 'auto', //  auto,auto,zh-CN,en,zh-TW,jp
        // https: true, // https  true
      }).then(function (vaptchaObj) {
        // obj = vaptchaObj // VAPTCHA
        // token
        // vaptchaObj.renderTokenInput('.login-form')//formtoken
        // token
        vaptchaObj.validate()
        vaptchaObj.listen('pass', function () {
          // 
          var data = {
            // 
            code: vaptchaObj.getToken()
          }
          callback(data)
          // $.post('login', data, function (r) {
          //   if (r.code !== 200) {
          //     console.log('')
          //     vaptchaObj.reset() // 
          //   }
          // })
        })
        // 
        vaptchaObj.listen('close', function () {
          // 
        })
      })
    }
  }
}
</script>

<style lang="scss" scoped>
</style>



